
.addBtn {
  -moz-box-shadow: 0px 0px 22px -4px #dcecfb;
  -webkit-box-shadow: 0px 0px 22px -4px #dcecfb;
  box-shadow: 0px 0px 22px -4px #dcecfb;
  background-color:#80b5ea;
  -moz-border-radius:42px;
  -webkit-border-radius:42px;
  border-radius:42px;
  display:inline-block;
  cursor:pointer;
  color:#ffffff;
  font-family:Courier New;
  font-size:15px;
  font-weight:bold;
  padding: 4px 4px 0px 5px;
  text-decoration:none;
  text-shadow:0px 1px 2px #dcecfb;
  width: 40px;
  height: 28px;


}
.addBtn:hover {
  background-color:#bddbfa;
}

.addBtn:active {
  position:relative;
  top:2px;
}
.remuveBtn {
  display: block;
  -moz-box-shadow: 0px 0px 22px -4px #f7c5c0;
  -webkit-box-shadow: 0px 0px 22px -4px #f7c5c0;
  box-shadow: 0px 0px 22px -4px #f7c5c0;
  background-color:#fc8d83;
  -moz-border-radius:42px;
  -webkit-border-radius:42px;
  border-radius:42px;
  cursor:pointer;
  color:#ffffff;
  font-family:Trebuchet MS;
  font-size:13px;
  font-weight:bold;
  padding:0.5px 118px;
  text-decoration:none;
  text-shadow:0px 1px 2px #b23e35;
  margin-top:7%;
}
.remuveBtn:hover {
  background-color:#e4685d;
}
.remuveBtn:active {
  position:relative;
  top:2px;
}
#mydiv{
  display: flex;
  align-items: center;
  justify-content: center;
  display: block;
}
.inp {
  -moz-box-shadow: 0px 0px 5px 5px #dcecfb;
  -webkit-box-shadow: 0px 0px 5px 5px #dcecfb;
  box-shadow: 0px 0px 5px 5px #dcecfb;
  background-color:#bddbfa;
  -moz-border-radius:8px;
  -webkit-border-radius:8px;
  border-radius:8px;
  display:inline-block;
  cursor:pointer;
  color:#ffffff;
  font-family:Trebuchet MS;
  font-size:19px;
  font-weight:bold;
  padding:5px 76px;
  text-decoration:none;
  text-shadow:0px 1px 2px #528ecc;
  margin: 0% 0% 0% 0%;
}
.inp:hover {
  background-color:#80b5ea;
}
.inp:active {
  position:relative;
  top:1px;
}
.App{
  justify-content: center;
  margin: 5% 0% 0% 35%;

}


.list {
  text-align: -webkit-center;
  height: 60px;
  width: 320px;
  margin-top: 3px;
  background-color:#33bdef;
  -moz-border-radius:8px;
  -webkit-border-radius:8px;
  border-radius:8px;
  display:block;
  cursor:pointer;
  color:#ffffff;
  font-family:Trebuchet MS;
  font-size:15px;
  font-weight:bold;
  text-decoration:none;
  text-shadow:-1px -2px 2px #5b6178;

}
.list:hover {
  background-color:#019ad2;
}
.list:active {
  position:relative;
  top:1px;
}


.line{box-shadow: 0px 0px 1px 2px #1c6b03;
    background-color: #3ab54a;

}
.line:hover {
  background-color: #63e4a9;
}
